.homepage{
    padding: 5em;
    background-color: rgb(64, 123, 201);
    min-height: 100vh;
}

.homepage h1{
    margin-bottom: 3em;
}

ul{
    list-style-type: none;
    max-width: 1200px;
    margin: 0 auto;
}

ul form{
    margin-bottom: 1em;
}

ul form input{
    border: none;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.463);
    padding: 0.5em 1em;
    display: inline-block;
}

ul form label{
    margin-left: 2em;
    font-weight: 600;
    color: white;
}

ul form select{
    outline: none;
    display: inline-block;
}

ul .head{
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
}

ul li{
    display: grid;
    grid-template-columns: 80px repeat(3, 2fr);
    justify-content: center;
    align-items: center;
    background-color: white;
}

ul p{
    border: 1px solid black;
    border-right: none;
    padding: 0.5em 1em;
    font-weight: 500;
}

ul p:first-child{
    text-align: center;
}

ul p:last-child{
    border-right: 1px solid black;
}