.loginForm{
    padding: 1em;
    background-color: white;
    border-radius: 10px;
    text-align: center;
    width: 100%;
    max-width: 400px;
}

.loginForm h1{
    margin-bottom: 1em;
}

.loginForm input{
    border: none;
    outline: none;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.463);
    border-radius: 5px;
    padding: 1em;
}

.loginForm button{
    all: unset;
    cursor: pointer;
    background-color: rgb(0, 110, 255);
    color: white;
    border-radius: 5px;
    padding: 0.5em 1.3em;
    text-transform: uppercase;
}

.name, .pass{
    margin-bottom: 1em;
}

span{
    display: inline-block;
    color: red;
    font-size: 0.8rem;
    width: 100%;
}